import React, { useState, useEffect, useRef } from "react";
import shaka from "shaka-player";

// Rest of the code remains the same

import "./Grid.css";
import playlistData from "./m3u/playlist.m3u";
import "shaka-player/dist/controls.css";
// import shaka from "shaka-player/dist/shaka-player.ui";

// If you also need the CSS styles, import them as well
import "shaka-player/dist/controls.css";

// import { useArrowKeys } from "react-keyboard-navigation";

function Grid() {
  const [playlist, setPlaylist] = useState([]);
  const [selectedUrl, setSelectedUrl] = useState("");
  const [activeIndex, setActiveIndex] = useState(0);
  const [showVideo, setShowVideo] = useState(true);
  const gridRef = useRef(null);
  const playerRef = useRef(null);

  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === "Escape" || event.key === "Backspace") {
        setShowVideo(false);
      }
    };

    document.addEventListener("keydown", handleKeyDown);

    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  }, []);

  useEffect(() => {
    if (!showVideo) {
      // Clean up Shaka Player and reset the video source
      const player = playerRef.current.shakaPlayer;
      if (player) {
        player.unload();
      }
      playerRef.current.src = "";
    }
  }, [showVideo]);

  useEffect(() => {
    if (showVideo) {
      // Initialize Shaka Player
      const player = new shaka.Player(playerRef.current);
      player.configure({
        streaming: {
          retryParameters: {
            maxAttempts: 3, // Maximum number of retries
          },
        },
      });

      // Load the HLS stream
      const manifestUri =
        "https://d2q8p4pe5spbak.cloudfront.net/bpk-tv/9XM/9XM.isml/index.m3u8";
      player
        .load(manifestUri)
        .then(() => {
          console.log("HLS stream loaded successfully");
        })
        .catch((error) => {
          console.error("Error loading HLS stream:", error);
        });

      return () => {
        // Clean up Shaka Player when the component unmounts or video is hidden
        player.unload();
      };
    }
  }, [showVideo]);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    // reader.onload = (e) => {
    //   const contents = e.target.result;
    //   const urls = parseM3U(contents);
    //   setPlaylist(urls);
    // };

    // reader.readAsText(file);
  };

  useEffect(() => {
    fetchPlaylist();
  }, []);

  const fetchPlaylist = () => {
    // Fetch the M3U file and parse its contents
    fetch(playlistData)
      .then((response) => response.text())
      .then((data) => {
        const parsedPlaylist = parseM3U(data);
        setPlaylist(parsedPlaylist);
      })
      .catch((error) => {
        console.error("Error fetching playlist:", error);
      });
  };

  const parseM3U = (contents) => {
    const lines = contents.split("\n");
    const urls = [];

    let currentUrl = "";
    let currentName = "";
    let currentImage = "";

    for (let i = 0; i < lines.length; i++) {
      const line = lines[i].trim();

      if (line.startsWith("#EXTINF")) {
        const info = line.split(",");

        // Extract the name from the line
        currentName = info[1].trim();

        // Extract the image URL from the line's tvg-logo attribute
        currentImage = extractImageUrl(line);
      } else if (line.startsWith("http")) {
        currentUrl = line.trim();

        urls.push({
          url: currentUrl,
          name: currentName,
          image: currentImage,
        });

        currentUrl = "";
        currentName = "";
        currentImage = "";
      }
    }

    return urls;
  };

  const extractImageUrl = (line) => {
    const regex = /tvg-logo="(.*?)"/; // Regular expression pattern to match the tvg-logo attribute and capture the URL
    const match = line.match(regex);

    if (match && match[1]) {
      return match[1]; // Extracted image URL
    }

    return process.env.PUBLIC_URL + "/logo192.png"; // Return default image URL if the attribute is not found or doesn't contain a valid URL
  };

  const handleItemClick = (url) => {
    setSelectedUrl(url);
    console.log(url);
    playUrl(url);
    // Play the URL
    // Add your own logic here to play the media
  };

  const playUrl = (url) => {
    // Initialize Shaka Player
    const player = new shaka.Player(playerRef.current);
    player.configure({
      streaming: {
        retryParameters: {
          maxAttempts: 3, // Maximum number of retries
        },
      },
    });

    // Load the HLS stream
    const manifestUri = url;
    player
      .load(manifestUri)
      .then(() => {
        console.log("HLS stream loaded successfully");
      })
      .catch((error) => {
        console.error("Error loading HLS stream:", error);
      });

    return () => {
      // Clean up Shaka Player when the component unmounts
      player.unload();
    };
  };

  const handleArrowKeys = (event) => {
    const { key } = event;

    if (key === "ArrowUp") {
      event.preventDefault(); // Prevent default scrolling behavior
      setActiveIndex((prevIndex) =>
        prevIndex > 0 ? prevIndex - 1 : prevIndex
      );
    } else if (key === "ArrowDown") {
      event.preventDefault(); // Prevent default scrolling behavior
      setActiveIndex((prevIndex) =>
        prevIndex < playlist.length - 1 ? prevIndex + 1 : prevIndex
      );
    } else if (key === "Enter") {
      handleItemClick(playlist[activeIndex].url);
    }
  };

  useEffect(() => {
    const handleKeyDown = (event) => {
      if (["ArrowUp", "ArrowDown", "Enter"].includes(event.key)) {
        handleArrowKeys(event);
      }
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [playlist]);

  useEffect(() => {
    if (gridRef.current) {
      const activeItem = gridRef.current.querySelector(".grid-item.active");
      if (activeItem) {
        activeItem.scrollIntoView({
          behavior: "smooth",
          block: "nearest",
          inline: "start",
        });
      }
    }
  }, [activeIndex]);

  return (
    <div>
      <video
        ref={playerRef}
        className="shaka-player-container"
        autoPlay
        controls
      />
      <div className="grid" ref={gridRef}>
        {playlist.map((item, index) => (
          <div
            key={index}
            className={`grid-item ${activeIndex === index ? "active" : ""}`}
            onClick={() => handleItemClick(item.url)}
            tabIndex={0}
            autoFocus={activeIndex === index}
          >
            <div className="grid-item-content">
              <div className="grid-item-name">{item.name}</div>
              <img
                src={item.image}
                alt="Thumbnail"
                className="grid-item-image"
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Grid;
